"use client";

import React from 'react';
import {
  CheckCircle2,
  XCircle,
  AlertTriangle,
  AlertCircle,
  HelpCircle,
  ExternalLink,
  ShieldQuestion
} from 'lucide-react';
import { Badge } from "@/components/ui/badge";
import { cn } from '@/lib/utils'; // Assuming you have this utility

// Define the expected structure for the result prop
interface SafetyResultData {
  originalUrl: string;
  status: 'SAFE' | 'UNSAFE' | 'ERROR' | 'INVALID_URL' | 'CONFIG_ERROR' | 'UNKNOWN';
  message: string;
}

interface SafetyResultProps {
  result: SafetyResultData;
}

const SafetyResult: React.FC<SafetyResultProps> = ({ result }) => {
  let IconComponent: React.ElementType;
  let iconColorClass: string;
  let badgeVariant: "default" | "destructive" | "secondary" | "outline" = "secondary";
  let statusText: string = result.status;

  switch (result.status) {
    case 'SAFE':
      IconComponent = CheckCircle2;
      iconColorClass = 'text-green-600 dark:text-green-500';
      badgeVariant = 'default'; // Use default (often green or primary) for safe
      statusText = 'Safe';
      break;
    case 'UNSAFE':
      IconComponent = XCircle;
      iconColorClass = 'text-red-600 dark:text-red-500';
      badgeVariant = 'destructive';
      statusText = 'Unsafe';
      break;
    case 'INVALID_URL':
      IconComponent = AlertTriangle;
      iconColorClass = 'text-orange-500 dark:text-orange-400';
      badgeVariant = 'outline';
      statusText = 'Invalid Input';
      break;
    case 'ERROR':
    case 'CONFIG_ERROR': // Treat config errors similarly to general errors visually
      IconComponent = AlertCircle;
      iconColorClass = 'text-yellow-600 dark:text-yellow-500';
      badgeVariant = 'secondary';
      statusText = 'Error';
      break;
    case 'UNKNOWN':
    default:
      IconComponent = ShieldQuestion; // More specific than HelpCircle
      iconColorClass = 'text-gray-500 dark:text-gray-400';
      badgeVariant = 'secondary';
      statusText = 'Unknown';
      break;
  }

  // Function to safely create a link
  const renderLink = (url: string) => {
    try {
      // Attempt to parse URL again for safety, although validated before
      const parsedUrl = new URL(url);
      // Only render links for safe URLs for extra caution?
      // if (result.status === 'SAFE') {
      //   return (
      //     <a
      //       href={parsedUrl.toString()} // Use parsed toString for consistency
      //       target="_blank"
      //       rel="noopener noreferrer"
      //       className="text-blue-600 dark:text-blue-400 hover:underline break-all inline-flex items-center"
      //     >
      //       {parsedUrl.toString()}
      //       <ExternalLink className="w-3 h-3 ml-1" />
      //     </a>
      //   );
      // }
      // For now, just display the text for all statuses
      return <span className="text-gray-700 dark:text-gray-300 break-all">{parsedUrl.toString()}</span>;
    } catch (_)
    {
        // If URL is somehow invalid at this stage, just display raw text
         return <span className="text-gray-500 dark:text-gray-400 break-all">{url} (Malformed)</span>;
    }
  };


  return (
    <div className="flex flex-col items-center p-6 border border-gray-200 dark:border-gray-700 rounded-lg bg-white dark:bg-gray-800 shadow-sm w-full">
      <IconComponent className={cn("w-16 h-16 mb-4", iconColorClass)} />

      <Badge variant={badgeVariant} className="mb-2 px-3 py-1 text-sm font-semibold">
        {statusText}
      </Badge>

      <p className="text-lg font-medium text-gray-900 dark:text-gray-100 mb-1">Scan Result</p>

      <div className="text-center max-w-full px-4 py-2 bg-gray-50 dark:bg-gray-700 rounded mb-4 overflow-x-auto">
         <p className="text-xs text-gray-500 dark:text-gray-400 mb-1">Detected URL:</p>
        {renderLink(result.originalUrl)}
      </div>


      <p className="text-center text-sm text-gray-600 dark:text-gray-300">
        {result.message}
      </p>
    </div>
  );
};

export default SafetyResult;
