"use client";

import React, { useState, useCallback } from 'react';
import QrScanner from '../components/qr-scanner'; // Relative import
import QrUploader from '../components/qr-uploader'; // Relative import
import SafetyResult from '../components/safety-result'; // Relative import
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Button } from '@/components/ui/button';
import { Loader2 } from 'lucide-react';
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"

// Define the structure for the safety result data
interface SafetyResultData {
  originalUrl: string;
  status: 'SAFE' | 'UNSAFE' | 'ERROR' | 'INVALID_URL' | 'CONFIG_ERROR' | 'UNKNOWN'; // Added UNKNOWN
  message: string;
}

export default function HomePage() {
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [resultData, setResultData] = useState<SafetyResultData | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [currentTab, setCurrentTab] = useState<string>("scan"); // To control scanner activity

  // Function to handle URL detected from scanner or uploader
  const handleUrlDetected = useCallback(async (url: string | null) => {
    if (!url) {
      setError("QR code does not contain readable data or is not a valid image."); // Modified error
      setResultData(null);
      setIsLoading(false);
      return;
    }

    console.log(`URL Detected: ${url}`);
    setIsLoading(true);
    setError(null);
    setResultData(null); // Clear previous result

    // Client-side URL validation (basic)
    try {
       new URL(url);
       // More robust checks can be added here if needed
       if (!url.startsWith('http://') && !url.startsWith('https://')) {
          throw new Error("URL must start with http:// or https://");
       }
    } catch (_)
    {
       console.log("Invalid URL format detected client-side");
        // Use a result state to show this info, similar to how the API does
        setResultData({
           originalUrl: url,
           status: 'INVALID_URL',
           message: 'The scanned data is not a valid web URL (must start with http:// or https://).'
        });
       setIsLoading(false);
       return;
    }


    // Call the Next.js API Route
    try {
      const response = await fetch('/api/check-url', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ url }),
      });

      const data: SafetyResultData | { error: string } = await response.json();

      if (!response.ok) {
        const errorMsg = (data as { error: string }).error || `Request failed with status ${response.status}`;
        throw new Error(errorMsg);
      }

      // Type guard to ensure data is SafetyResultData
      if ('status' in data) {
         setResultData(data);
      } else {
         // This case should ideally be covered by !response.ok, but good practice
         throw new Error("Received unexpected response format from server.");
      }

    } catch (err: any) {
      console.error("Error checking URL safety:", err);
      setError(err.message || "An unexpected error occurred.");
      setResultData(null); // Clear result on error
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Function to reset the state and allow a new scan/upload
  const handleReset = () => {
    setIsLoading(false);
    setError(null);
    setResultData(null);
    // Consider resetting the uploader's file input if needed
  };

  // Handler for errors from child components, with explicit type
  const handleError = useCallback((errMsg: string) => {
      setError(errMsg);
  }, []);

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-4 md:p-12 bg-gradient-to-br from-gray-50 to-gray-200 dark:from-gray-900 dark:to-gray-800">
      <Card className="w-full max-w-lg shadow-xl dark:bg-gray-850">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl font-bold tracking-tight">QR Code Link Safety Checker</CardTitle>
        </CardHeader>
        <CardContent>
          {/* Display Error if any */}
          {error && (
            <Alert variant="destructive" className="mb-4">
              <AlertTitle>Error</AlertTitle>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/* Display Loading Indicator */}
          {isLoading && (
            <div className="flex flex-col items-center justify-center p-8">
              <Loader2 className="h-12 w-12 animate-spin text-blue-600" />
              <p className="mt-4 text-lg text-gray-700 dark:text-gray-300">Checking URL safety...</p>
            </div>
          )}

          {/* Display Result if available */}
          {!isLoading && resultData && (
            <div className="flex flex-col items-center">
                <SafetyResult result={resultData} />
                <Button onClick={handleReset} variant="outline" className="mt-6">Scan/Upload Another</Button>
            </div>
          )}

          {/* Display Scanner/Uploader Tabs if not loading and no result */}
          {!isLoading && !resultData && (
             <Tabs value={currentTab} onValueChange={setCurrentTab} defaultValue="scan" className="w-full">
                <TabsList className="grid w-full grid-cols-2">
                    <TabsTrigger value="scan">Scan with Camera</TabsTrigger>
                    <TabsTrigger value="upload">Upload Image</TabsTrigger>
                </TabsList>
                <TabsContent value="scan">
                    <QrScanner
                        onUrlDetected={handleUrlDetected}
                        onError={handleError} // Use the typed handler
                        isActive={currentTab === 'scan'} // Only active when tab is selected
                    />
                    <p className="mt-2 text-sm text-center text-gray-500 dark:text-gray-400">Point your camera at a QR code.</p>
                </TabsContent>
                <TabsContent value="upload">
                    <QrUploader
                        onUrlDetected={handleUrlDetected}
                        onError={handleError} // Use the typed handler
                    />
                     <p className="mt-2 text-sm text-center text-gray-500 dark:text-gray-400">Upload an image file containing a QR code.</p>
                </TabsContent>
            </Tabs>
          )}

        </CardContent>
      </Card>
       <p className="mt-8 text-xs text-gray-500 dark:text-gray-400 text-center max-w-lg">
           Disclaimer: This tool uses external services like Google Safe Browsing to check URL safety. Results are indicative and not guaranteed to be 100% accurate. Always exercise caution when clicking links.
       </p>
    </main>
  );
}
