"use client";

import React, { useState, useEffect } from 'react';
import { QrReader } from 'react-qr-reader'; // Use the specific component
import { Loader2, RefreshCw } from 'lucide-react';

interface QrScannerProps {
  onUrlDetected: (url: string | null) => void;
  onError: (message: string) => void;
  isActive: boolean; // To control camera activity based on tab selection
}

const QrScanner: React.FC<QrScannerProps> = ({ onUrlDetected, onError, isActive }) => {
  const [cameraError, setCameraError] = useState<string | null>(null);
  const [isClient, setIsClient] = useState(false);

  // The QrReader component needs to run client-side only
  useEffect(() => {
    setIsClient(true);
  }, []);

  const handleResult = (result: any, error: any) => {
     // Early exit if the scanner is not active
     if (!isActive) return;

    if (result) {
       // Ensure result has text property and it's not empty
      const scannedText = result?.getText();
       if (scannedText) {
         console.log("QR Scan Result:", scannedText);
         onUrlDetected(scannedText);
       } else {
          // Optional: Handle cases where QR is detected but has no text (rare)
          // console.log("QR code detected but no text found.");
       }
    }

    if (error) {
        // Ignore common "NotFoundException" errors which occur when no QR code is in view
        // This prevents spamming the error state.
        if (error.name === 'NotFoundException') {
             // console.log('No QR code found in frame.');
             setCameraError(null); // Clear previous actual errors
             return;
        }

        console.error("QR Scanner Error:", error);
        let errorMessage = "An unknown camera error occurred.";
        if (error.name === 'NotAllowedError') {
            errorMessage = "Camera permission denied. Please allow camera access in your browser settings.";
        } else if (error.name === 'NotReadableError') {
            errorMessage = "Could not access the camera. Is it already in use by another application?";
        } else if (error.name === 'OverconstrainedError') {
            errorMessage = "Could not find a suitable camera. Please ensure a camera is connected and enabled.";
        } else {
            errorMessage = `Camera error: ${error.message || error.name}`;
        }
        setCameraError(errorMessage);
        onError(errorMessage); // Propagate error up
    }
  };

  const handleRetry = () => {
    setCameraError(null);
    onError(""); // Clear error in parent as well
    // The component should ideally retry automatically or provide a way to re-init
    // For now, clearing the error might be enough if the underlying issue is resolved
  };

  // Render placeholder or nothing server-side
  if (!isClient) {
    return (
         <div className="flex items-center justify-center h-64 bg-gray-100 dark:bg-gray-700 rounded-md">
             <Loader2 className="h-8 w-8 animate-spin text-gray-500" />
        </div>
    );
  }

  // If not active, show a placeholder instead of activating the camera
  if (!isActive) {
     return (
        <div className="flex items-center justify-center h-64 bg-gray-100 dark:bg-gray-700 rounded-md">
            <p className="text-gray-500 dark:text-gray-400">Camera inactive</p>
        </div>
     );
  }

  // Render Camera Error state
   if (cameraError) {
    return (
        <div className="flex flex-col items-center justify-center h-64 bg-red-50 dark:bg-red-900/30 text-red-700 dark:text-red-300 p-4 rounded-md text-center">
            <p className="mb-4">{cameraError}</p>
            <button
                onClick={handleRetry}
                className="flex items-center px-3 py-1 border border-red-300 dark:border-red-600 rounded text-sm hover:bg-red-100 dark:hover:bg-red-800/50 transition-colors"
            >
                <RefreshCw className="w-4 h-4 mr-1" />
                Retry
            </button>
        </div>
    );
  }

  // Render the QR Reader component
  return (
    <div className="relative w-full pt-[75%] bg-black rounded-md overflow-hidden shadow-inner"> {/* Maintain aspect ratio, e.g., 4:3 */} 
      <QrReader
        // `key` helps in re-initializing if needed, though often not necessary
        onResult={handleResult}
        constraints={{
            facingMode: 'environment' // Prefer back camera
        }}
        // Removed `delay` and `style` as they might cause issues with newer versions or types
        // The component should handle its own styling and layout within the container
        containerStyle={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%' }}
        videoContainerStyle={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%' }}
        videoStyle={{ objectFit: 'cover', width: '100%', height: '100%' }}
      />
       {/* Optional: Add an overlay element here if desired */}
       {/* <div className="absolute inset-0 border-4 border-green-500 opacity-50" style={{ clipPath: 'inset(25% 25% 25% 25% round 10px)' }}></div> */}
    </div>
  );
};

export default QrScanner;
