"use client";

import React, { useState, useRef, useCallback } from 'react';
import jsQR from 'jsqr';
import { UploadCloud, Loader2, AlertCircle } from 'lucide-react';
import { Button } from "@/components/ui/button";

interface QrUploaderProps {
  onUrlDetected: (url: string | null) => void;
  onError: (message: string) => void;
}

const QrUploader: React.FC<QrUploaderProps> = ({ onUrlDetected, onError }) => {
  const [isProcessing, setIsProcessing] = useState<boolean>(false);
  const [feedbackMessage, setFeedbackMessage] = useState<string | null>(null);
  const [isError, setIsError] = useState<boolean>(false);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);

  const handleFileChange = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) {
      return;
    }

    setIsProcessing(true);
    setFeedbackMessage('Processing image...');
    setIsError(false);
    onError(""); // Clear previous errors in parent

    const reader = new FileReader();

    reader.onload = (e) => {
      const dataUrl = e.target?.result as string;
      if (!dataUrl) {
        setFeedbackMessage('Could not read file data.');
        setIsError(true);
        onError('Could not read file data.');
        setIsProcessing(false);
        return;
      }

      const image = new Image();
      image.onload = () => {
        const canvas = canvasRef.current;
        const ctx = canvas?.getContext('2d', { willReadFrequently: true }); // Optimize for frequent reads

        if (!canvas || !ctx) {
          setFeedbackMessage('Canvas element not available.');
          setIsError(true);
           onError('Canvas element not available.');
          setIsProcessing(false);
          return;
        }

        // Adjust canvas size to image dimensions
        canvas.width = image.naturalWidth;
        canvas.height = image.naturalHeight;

        // Draw image onto canvas
        ctx.drawImage(image, 0, 0, canvas.width, canvas.height);

        try {
          // Get image data from canvas
          const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);

          // Decode QR code using jsQR
          const code = jsQR(imageData.data, imageData.width, imageData.height, {
            inversionAttempts: 'dontInvert', // Or 'attemptBoth' if needed
          });

          if (code && code.data) {
            console.log("Decoded QR from Upload:", code.data);
            setFeedbackMessage('QR Code detected!');
            setIsError(false);
            onUrlDetected(code.data);
          } else {
            setFeedbackMessage('No QR code found in the uploaded image.');
             setIsError(true);
             onError('No QR code found in the uploaded image.') // Inform parent
             onUrlDetected(null); // Ensure parent knows no URL was found
          }
        } catch (err) {
           console.error("Error decoding QR code:", err);
           setFeedbackMessage('Error processing image for QR code.');
           setIsError(true);
           onError('Error processing image for QR code.')
           onUrlDetected(null);
        }
         finally {
           // Clear canvas after processing
           // ctx.clearRect(0, 0, canvas.width, canvas.height);
           setIsProcessing(false);
           // Reset file input to allow uploading the same file again if needed
           if (fileInputRef.current) {
                fileInputRef.current.value = "";
           }
         }
      };

      image.onerror = () => {
        setFeedbackMessage('Could not load image file. Is it a valid image?');
        setIsError(true);
        onError('Could not load image file.');
        setIsProcessing(false);
      };

      image.src = dataUrl;
    };

    reader.onerror = () => {
       setFeedbackMessage('Error reading file.');
       setIsError(true);
       onError('Error reading file.');
       setIsProcessing(false);
    };

    reader.readAsDataURL(file);
  }, [onUrlDetected, onError]);

  const triggerFileInput = () => {
    fileInputRef.current?.click();
  };

  return (
    <div className="flex flex-col items-center justify-center p-4 border-2 border-dashed border-gray-300 dark:border-gray-600 rounded-md h-64">
      <input
        type="file"
        ref={fileInputRef}
        onChange={handleFileChange}
        accept="image/*" // Accept common image types
        className="hidden" // Hide the default input
        disabled={isProcessing}
      />
      {/* Hidden canvas used for image processing */}
      <canvas ref={canvasRef} style={{ display: 'none' }}></canvas>

      {isProcessing ? (
        <div className="flex flex-col items-center text-center">
           <Loader2 className="h-8 w-8 animate-spin text-blue-500 mb-2" />
           <p className="text-sm text-gray-600 dark:text-gray-400">{feedbackMessage || 'Processing...'}</p>
        </div>
      ) : (
        <div className="flex flex-col items-center text-center">
          <UploadCloud className="h-12 w-12 text-gray-400 dark:text-gray-500 mb-3" />
          <Button onClick={triggerFileInput} variant="outline" size="sm">
            Choose Image File
          </Button>
          <p className="mt-2 text-xs text-gray-500 dark:text-gray-400">Select an image containing a QR code.</p>
            {feedbackMessage && (
                <p className={`mt-3 text-sm ${isError ? 'text-red-600 dark:text-red-400' : 'text-green-600 dark:text-green-400'}`}>
                 {isError && <AlertCircle className="inline w-4 h-4 mr-1"/>}
                 {feedbackMessage}
                </p>
            )}
        </div>
      )}
    </div>
  );
};

export default QrUploader;
